/**
 * 
 * // ===  === //
 * // === 1.  prototypeproto === //
 * // === 2.  proto constructor === //
 * // === 3. proto === //
 * @param parent 
 * @param child 
 */
export function inheritProto (parent, child) {
  const proto = Object.create(parent.prototype)
  proto.constructor = child
  child.prototype = proto
}