/**
 * 
 * // ===  === //
 * // ===  === //
 * // === 1DOM2ie(attachEvent) ie(addEventListener)=== //
 * // === 2DOM0onxxx === //
 * @param {Node} node 
 * @param {String} type 
 * @param {Function} handler 
 * @param {Boolean} useCap true: false:
 */
export function on (node, type, handler, useCap = false) {
  // DOM2ie
  if (node.addEventListener) {
    node.addEventListener(type, handler, useCap)
  }
  // DOM2ie
  else if (node.attachEvent) {
    node.attachEvent(`on${type}`, handler)
  }
  // DOM0
  else {
    node[`on${type}`] = handler
  }
}

/**
 * 
 * // ===  === //
 * // ===  === //
 * // === 1DOM2ie(detachEvent) ie(removeEventListener)=== //
 * // === 2DOM0onxxx = null === //
 * @param {Node} node 
 * @param {String} type 
 * @param {Function} handler 
 * @param {Boolean} useCap true: false:
 */
export function off (node, type, handler,  useCap = false) {
  // DOM2ie
  if (node.removeEventListener) {
    node.removeEventListener(type, handler, useCap)
  }
  // DOM2ie
  else if (node.detachEvent) {
    node.attachEvent(`on${type}`, handler)
  }
  // DOM0
  else {
    node[`on${type}`] = null
  }
}

/**
 * 
 * // === ie(window.event) === //
 * @param e
 * @return {*|Event}
 */
export function getEvent (e) {
  return e || window.event
}

/**
 * 
 * // === e.currentTarget -  === //
 * // === ie - e.srcElement === //
 * @param {Object} e
 * @return {Object}
 */
export function getTarget (e) {
  return e.target || e.srcElement
}

/**
 * 
 * // === ie - e.returnValue = true === //
 * @param e
 */
export function preventDefault (e) {
  if (e.preventDefault) {
    e.preventDefault()
  } else {
    e.returnValue = true
  }
}

/**
 * 
 * // === ie - e.cancelBubble = true === //
 * // === stopImmediatePropagation -  === //
 * @param e
 */
export function stopPropagation (e) {
  if (e.stopPropagation) {
    e.stopPropagation()
  } else {
    e.cancelBubble = true
  }
}