/**
 * Date
 * Date.now(): 197011 00:00:00 UTC Unix 1000
 * +new Date(): Date.now()
 */

/**
 * 
 * // ===
 *  
 *   -- 
 * === //
 * @param fn
 * @param delay
 * @return {Function}
 */
export function throttle (fn, delay = 250) {
  let timer
  let last

  return function () {
    const context = this
    const args = arguments
    const now = +new Date()

    // fn delay, fn
    if (last && now < last + delay) {
      clearTimeout(timer)

      //  fn
      timer = setTimeout(function () {
        last = now
        fn.apply(context, args)
      }, delay)
    }
    // 
    else {
      last = now
      fn.apply(context, args)
    }
  }
}

/**
 * 
 * // ===
 * 
 * 10
 * 10wait
 * === //
 * @param {Function} fn 
 * @param {Number} delay 
 * @param {Boolean} immediate 
 * @return {Function}
 */
export function debounce (fn, delay = 250, immediate) {
  let timer

  //  delay  fn 
  return function () {
    const context = this
    const args = arguments

    //  fn
    timer && clearTimeout(timer)

    // immediate = true
    if (immediate) {
      timer = setTimeout(function () {
        timer = null
      }, delay)
      !timer && fn.apply(context, args)
    }
    // immediate = false
    else {
      timer = setTimeout(function () {
        fn.apply(context, args)
      }, delay)
    }
  }
}

