import {on, off, getEvent, getTarget, preventDefault} from '../../lib/event'
import {throttle} from '../../lib/timer'
/**
 * // ===  === //

 * // === === //

 * // ===
 * clientXclientY: 
 * ===//

 * // ===
 * offsetParent: offsetParent  the nearest table, table cell or root elementhtmlbody
 * offsetLeft: offsetParentleft
 * offsetTopoffsetParenttop
 * display:noneposition: fixednull
 * ie9position: fixednulldisplay:none
 * ===//

 * // ===
 *  attribute  property
 *  1attributeHTMLDOMpropertyDOMdocument.getElementById('test').foo = 1
 *  2attributeStringproperty
 *
 *  attribute  property
 *  1attributeidtitle .property 
 *  2propertyattribute<div id="test" class="button"></div>  div.getAttribute('class')  div.setAttribute('class','green-input')
 *  3attributepropertyinputvalueinput.value = 'foo2'; input.getAttribute('value'); // string"foo"
 *
 *  
 *  1JavaScriptpropertypropertyattribute
 *  2attribute
 *
 *  
 *  1 class JavaScriptpropertyclass className
 * === //
 */

/**
 * 
 * // ===  === //
 * // ===
 *  1onmousedown
 *     onmousemove
 *     onmouseupmousedownmousemovemouseup
 * === //
 * // ===
 *  2
 *  aonmousedowndiffX = e.clientX - ele.offsetLeft; diffY = e.clientY - ele.offsetTop;
 *  bonmousemovediffX  diffYdragging.style.left = e.clientX - diffX; dragging.style.top = e.clientY - diffY;
 * === //
 */
const DragDrop = (function () {

  let dragging = null
  let diffX = 0
  let diffY = 0

  function handleEvent (e) {
    // 
    let ev = getEvent(e)
    const target = getTarget(ev)
    let $wrapper = this.$wrapper


    // 
    switch (ev.type) {
      case 'mousedown':
        if (target.getAttribute('jm-drag')) {
          dragging = target

          diffX = ev.clientX - $wrapper.offsetLeft
          diffY = ev.clientY - $wrapper.offsetTop

          // 
          return false
        }
        break
      case 'mousemove':
        if (dragging !== null) {
          // clientWidth
          preventDefault(ev)
          $wrapper.style.left = ev.clientX - diffX + 'px'
          $wrapper.style.top = ev.clientY - diffY + 'px'
        }
        break
      case 'mouseup':
        dragging = null
        break
    }
  }

  // 
  const throttleEvent = function (self) {
    return throttle(handleEvent.bind(self), 5)
  }

  return {
    enable: function () {
      on(document, 'mousedown', handleEvent.bind(this))
      on(document, 'mousemove', throttleEvent(this))
      on(document, 'mouseup', handleEvent.bind(this))
      return this
    },
    disable: function () {
      off(document, 'mousedown', handleEvent)
      off(document, 'mousemove', throttleEvent(this))
      off(document, 'mouseup', handleEvent)
      return this
    }
  }
})()

export default DragDrop