// 
const initialState = {
  articleList: [], // 
  articleDetail: {}, // 
  currentPage: 1, // 
  total: 0, // 
  newArticleData: {
    title: '', // 
    content: '', // 
    Tag_id: 1, // 
    Category_id: 1, // 
    id: '', // ID
    imgSrc: '', // 
    foreword: '' // 
  }
}

// ation: 
export const actionTypes = {
  GET_ALL_ARTICLES: 'GET_ALL_ARTICLES', // 
  RESPONSE_ALL_ARTICLES: 'RESPONSE_ALL_ARTICLES', // 
  GET_ARTICLE_DETAIL: 'GET_ARTICLE_DETAIL', // 
  RESPONSE_ARTICLE_DETAIL: 'RESPONSE_ARTICLE_DETAIL', // 
  GET_IS_OR_NOT_PUBLISHED_ARTICLES: 'GET_IS_OR_NOT_PUBLISHED_ARTICLES', // 
  RESPONSE_IS_OR_NOT_PUBLISHED_ARTICLES: 'RESPONSE_IS_OR_NOT_PUBLISHED_ARTICLES', // 
  GET_ISNOTPUBLISHED_ARTICLES: 'GET_ISNOTPUBLISHED_ARTICLES', // 
  RESPONSE_ISNOTPUBLISHED_ARTICLES: 'RESPONSE_ISNOTPUBLISHED_ARTICLES', // 

  // SET_ARTICLE_ID: 'SET_ARTICLE_ID', // ID
  POST_SAVE_ARTICLE: 'POST_SAVE_ARTICLE', // 
  POST_PUBLISH_ARTICLE: 'POST_PUBLISH_ARTICLE', // 
  POST_CANCEL_ARTICLE: 'POST_CANCEL_ARTICLE', // 

  PUT_MODIFY_ARTICLE: 'PUT_MODIFY_ARTICLE', // 

  DELETE_ARTICLE: 'DELETE_ARTICLE', // 

  UPDATING_TITLE: 'UPDATING_TITLE', // 
  UPDATING_CONTENT: 'UPDATING_CONTENT', // 
  UPDATING_TAG: 'UPDATING_TAG', // 
  UPDATING_Category: 'UPDATING_Category', // 
  UPDATING_SHOWED_IMG_URL: 'UPDATING_SHOWED_IMG_URL', // 
  UPDATING_FOREWORD: 'UPDATING_FOREWORD', // 

}

// action creatoraction
export const actions = {
  /**
   * @example {
 *    pageNum: paramsObj.pageNum,
      pageSize: paramsObj.pageSize,
      tag: paramsObj.tag,
      category: paramsObj.category
 * }
   * @type {{get_all_articles: actions.get_all_articles, get_article_detail: actions.get_article_detail}}
   */
  get_all_articles: function (paramsObj) {
    return {
      type: actionTypes.GET_ALL_ARTICLES,
      paramsObj
    }
  },
  /**
   * 
   * @param paramsObj
   * @example {
   *  pageNum: paramsObj.pageNum,
      pageSize: paramsObj.pageSize,
      isPublished: 1() 0
   * }
   * @return {{type: string, paramsObj: *}}
   */
  get_is_or_not_pubished_articles: function (paramObj) {
    return {
      type: actionTypes.GET_IS_OR_NOT_PUBLISHED_ARTICLES,
      paramObj
    }
  },
  /**
   *  action creator
   * @param id
   * @return {{type: string, id: *}}
   */
  get_article_detail: function (id) {
    return {
      type: actionTypes.GET_ARTICLE_DETAIL,
      id
    }
  },
  /**
   * 
   * @param title
   * @return {{type: string}}
   */
  update_title: function (title) {
    return {
      type: actionTypes.UPDATING_TITLE,
      title
    }
  },
  /**
   * 
   * @param imgSrc
   * @return {{type: string}}
   */
  update_showed_img_url: function (imgSrc) {
    return {
      type: actionTypes.UPDATING_SHOWED_IMG_URL,
      imgSrc
    }
  },
  /**
   * 
   * @param foreword
   * @return {{type: string}}
   */
  update_foreword: function (foreword) {
    return {
      type: actionTypes.UPDATING_FOREWORD,
      foreword
    }
  },
  /**
   * 
   * @param content
   * @return {{type: string, content: *}}
   */
  update_content: function (content) {
    return {
      type: actionTypes.UPDATING_CONTENT,
      content
    }
  },
  /**
   * 
   * @param Tag_id
   * @return {{type: *, tag: *}}
   */
  update_tag:function (Tag_id) {
    return{
      type:actionTypes.UPDATING_TAG,
      Tag_id
    }
  },
  /**
   * 
   * @param Category_id
   * @return {{type: string, category: *}}
   */
  update_category:function (Category_id) {
    return{
      type:actionTypes.UPDATING_Category,
      Category_id
    }
  },
  /**
   * 
   * @param data
   * @param csrfToken
   * @example {
   * articleData: {}
   * }
   * @return {{type: string, data: *}}
   */
  save_article: function (postData) {
    return {
      type:actionTypes.POST_SAVE_ARTICLE,
      data: postData
    }
  },
  /**
   * 
   * @param postData
   * @example {
   * articleData: {}
   * }
   * @return {{type: string, data: *}}
   */
  modify_article: function (pageNum, pageSize, postData) {
    return {
      type:actionTypes.PUT_MODIFY_ARTICLE,
      data: postData,
      pageNum,
      pageSize
    }
  },
  /**
   * 
   * @param id
   * @param pageNum
   * @param pageSize
   * @return {{type: string, id: *, pageSize: *, pageNum: *}}
   */
  delete_an_article: function (id, pageNum, pageSize) {
    return {
      type:actionTypes.DELETE_ARTICLE,
      id,
      pageNum,
      pageSize
    }
  }
}

/**
 * reducer
 * @param state
 * @param action
 * @return {*}
 */
export function reducer (state = initialState, action) {
  switch (action.type) {
    case actionTypes.RESPONSE_ALL_ARTICLES:
      return {
        articleList: action.data,
        total: action.total
      }
    case actionTypes.RESPONSE_ARTICLE_DETAIL:
      return {
        articleDetail: action.data
      }
    case actionTypes.RESPONSE_IS_OR_NOT_PUBLISHED_ARTICLES:
      return {
        articleList: action.data
      }
    case actionTypes.UPDATING_TITLE:
      return {
        newArticleData: Object.assign({}, state.newArticleData, {
          title: action.title
        })
      }
    case actionTypes.UPDATING_FOREWORD:
      return {
        newArticleData: Object.assign({}, state.newArticleData, {
          foreword: action.foreword
        })
      }
    case actionTypes.UPDATING_SHOWED_IMG_URL:
      return {
        newArticleData: Object.assign({}, state.newArticleData, {
          imgSrc: action.imgSrc
        })
      }
    case actionTypes.UPDATING_CONTENT:
      return {
        newArticleData: Object.assign({}, state.newArticleData, {
          content: action.content
        })
      }
    case actionTypes.UPDATING_TAG:
      return {
        newArticleData: Object.assign({}, state.newArticleData, {
          Tag_id: action.Tag_id
        })
      }
    case actionTypes.UPDATING_Category:
      return {
        newArticleData: Object.assign({},state.newArticleData, {
          Category_id: action.Category_id
        })
      }
    default:
      return state
  }
}
