import DragDrop from './drag-drop'
import { replaceClass } from '../../lib/className'

/**
 * // === document.createElement(tagName) === //
 * // === parent.appendChild(newNode) === //
 * // === parent.insertBefore(newNode,referenceNode) === //
 * // === parent.removeChild(tagName) === //
 * // === parent.replaceChild(newChild, oldChild) === //
 * // === node.cloneNode([true: | false:]) === //
 */

/**
 * innerText textContentnodeValue
 * // ===
 * textContent:
 * 1documentdocument typenotationnull
 * 2processing instruction
 * 3
 * === //
 *
 * // ===
 * innerText:
 * a property that represents the "rendered" text content of a node and its descendants
 * === //
 *
 * // ===
 * nodeValue:
 * 1nullDocument DocumentFragment Element NamedNodeMap EntityReference Notation
 * 2or text, comment, and CDATA nodes, nodeValue returns the content of the node
 * 3For attribute nodes, the value of the attribute is returned.
 * 4nodeValueNull
 * === //
 *
 * // ===
 * innerText  textContentd
 * 1textContent<script><style>innerText
 * 2textContent innerText
 * 3textContentinnerText
 * 4ie11innerText HTML 
 * (so it is impossible to insert the nodes again into any other element or into the same element anymore) textContent
 * === //
 */

/**
 * return this
 * 
 * 
 */

/**
 * 
 * @param config
 * @constructor
 */
function Popup () {
  this.$wrapper = null
  this.$headerTitle = null
  this.$body = null
  this.$sureBtn = null
  this.$cancelBtn = null
}

Popup.prototype = {
  // ===  === //
  constructor: Popup,
  /**
   * html
   */
  createBaseHtml: function () {
    // === documentdocumentdocument === //
    const doc = document
    // === DocumentFragment DOMDOMDocumentFragment  === //
    const frag = doc.createDocumentFragment()

    const $wrapper = doc.createElement('div')
    $wrapper.className = `popup popup-open`
    $wrapper.setAttribute('jm-drag', 'true')

    // 
    const $header = doc.createElement('header')
    $header.className = `popup-header`
    const $headerTitle = doc.createElement('h2')
    $headerTitle.className = `header-title`
    $header.appendChild($headerTitle)

    // body
    const $body = doc.createElement('div')
    $body.className = `popup-content`

    // footer
    const $footer = doc.createElement('footer')
    $footer.className = `popup-footer`
    // const $sureBtn = doc.createElement('button')
    // $sureBtn.type = 'button'
    // $sureBtn.className = `sure-btn`
    // $sureBtn.textContent = ''
    // const $cancelBtn = doc.createElement('button')
    // $cancelBtn.type = 'button'
    // $cancelBtn.className = `cancel-btn`
    // $cancelBtn.textContent = ''
    // $footer.appendChild($sureBtn)
    // $footer.appendChild($cancelBtn)

    // 
    $wrapper.appendChild($header)
    $wrapper.appendChild($body)
    $wrapper.appendChild($footer)
    frag.appendChild($wrapper)
    doc.body.appendChild(frag)

    // 
    this.$wrapper = $wrapper
    this.$headerTitle = $headerTitle
    this.$body = $body
    this.$footer = $footer
    // this.$sureBtn = $sureBtn
    // this.$cancelBtn = $cancelBtn

    return this
  },
  setFooter: function () {},
  /**
   * 
   */
  setContent: function (content) {
    this.$body.innerHTML = content
    return this
  },
  /**
   * 
   */
  setHeader: function (title) {
    this.$headerTitle.textContent = title
    return this
  },
  /**
   * 
   */
  setStyle: function () {
    const style = document.createElement('style')
    style.innerHTML = `
      .popup {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 600px;
        padding: 10px 20px;
        border: 1px solid #999999;
        border-radius: 10px;
        box-shadow: rgba(0, 0, 0, 0.3) 2px 2px 4px;
        background: white;
        }
        .popup-header {
            padding-bottom: 5px;
            border-bottom: 1px solid #999999;
        }
        .popup-header .header-title{
            font: 18px/24px '';
            margin: 0;
        }
        .popup-content {
            margin: 10px 0;
            word-break: break-all;
        }
        .popup-footer {
            text-align: right;
        }
        .popup-footer button {
            font: 14px/1.5 '';
            padding: 0 12px;
            color: white;
            border-radius: 5px;
            border: 1px solid #999999;
            background: dodgerblue;
            cursor: pointer;
        }
         .popup-footer button:first-of-type {
            margin-right: 10px;
        }
        .popup-footer .cancel-btn {
            color: #999999;
            background: white;
        }
        .popup-footer .cancel-btn:hover {
            background: #eeeeee;
        }
        .popup-open {
            animation: zoomIn 0.3s linear;
        }
        .popup-close {
            animation: zoomOut 0.3s linear forwards;
        }
        .popup-input {
            display: block;
            width: 80%;
            margin: 10px 0;
            border: 1px solid #eee;
            background: white;
        }
        @keyframes zoomIn {
            from {
                transform: translate(-50%, -50%) scale(0, 0);
            }
            to {
                transform: translate(-50%, -50%) scale(1, 1);
            }
        }
        @keyframes zoomOut {
            from {
                transform: translate(-50%, -50%) scale(1, 1);
            }
            to {
                transform: translate(-50%, -50%) scale(0, 1);
            }
        }
    `
    // headdocument.head
    document.head.appendChild(style)

    return this
  },
  /**
   * 
   */
  open: function () {
    replaceClass(this.$wrapper, 'popup-close', 'popup-open')
  },
  /**
   * 
   */
  close: function () {
    replaceClass(this.$wrapper, 'popup-open', 'popup-close')
  },
  move: DragDrop.enable,
  init: function (config) {}
}



export default Popup